@import url(./common/reset.less);
@import url(./common/common.less);
@import url(./common/header.less);
@import url(./common/footer.less);
body {
  min-height: 100%;
  height: 100%;
  margin: 0 auto;
}
.img{
  transform: translate(-50%);
  background: #000;
}
.video-wrapper {
  position: relative;
  min-width: 1200px;
  height: 100%;
  .video-box {
    z-index: -100;
    font-size: 0;
    width: 100%;
    video {
      width: 100%;
    }
  }
  .mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    background-color: rgba(0,0,0,.4);
    z-index: 100;
  }
  .mask-content {
    position: absolute;
    top: 45%;
    width: 100%;
    text-align: center;
    h1 {
      font-size: 70px;
      color: #fff;
    }
    p {
      color: #fff;
      font-size: 28px;
      max-width: 700px;
      margin: 0 auto;
    }
  }
}
.img-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: red;
  height: 500px;
}

